<template>
  <div class="blb-table">
    <div class="table">
      <table>
        <tr class="title">
          <th :key="column.key" v-for="column in columns">{{column.title}}</th>
        </tr>
        <tr :key="datas.id" v-for="datas in tableDatas" class="content">
          <td :key="renderKey" v-for="(renderKey, index) in renderKeys">
            <!-- 渲染不同类型组件 -->
            <input :value="datas[renderKey]" @change="handleChange(getComponentType(index))" class="inputNunber" v-if="getComponentType(index) === 'inputNumber'" type="number">
            <span v-else v-html="renderItem(datas, renderKey, index)"></span>
          </td>
        </tr>
      </table>
    </div>
    <div class="blb-1px-b"></div>
  </div>
</template>

<script>
import _ from 'lodash'
const COMPONENT_NAME = 'blb-table'
export default {
  name: COMPONENT_NAME,
  props: {
    dataSources: {
      type: Array,
      default: null
    },
    columns: {
      type: Array,
      default: null
    }
  },
  data () {
    return {
      renderKeys: [],
      tableDatas: this.dataSources || []
    }
  },
  computed: {
  },
  methods: {
    // 储存标识key
    saveKeys () {
      const { columns } = this
      let keys = _.cloneDeep(this.renderKeys)
      columns && columns.map((column) => {
        if (keys.indexOf(column.key) === -1) {
          keys.push(column.key)
        }
      })
      this.renderKeys = keys
    },
    // 获取需要渲染组件类型
    getComponentType (index) {
      return this.columns[index]['componentTypes']
    },
    // 组件change函数
    handleChange (componentTypes) {
      this.$emit('handleChange', componentTypes, this.tableDatas)
    },
    // 渲染函数
    renderItem (item, renderKey, index) {
      const { columns } = this
      if (columns[index] && columns[index].renderEvent) {
        return columns[index].renderEvent.call(this, item)
      }
      return (item && item[renderKey]) || '无'
    }
  },
  mounted () {
    this.saveKeys()
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .blb-table
    font-size 28px
    color #4a4a4a
  .table
    padding-bottom 22px
  .title
  table
    width 100%
  th
    position relative
    &:after
      content: " ";
      position: absolute;
      left: 0;
      bottom: 0;
      right: 0;
      height: 1px;
      border-bottom: 1px solid #e5e5e5
      color: #e5e5e5
      transform-origin: 0 100%
      transform: scaleY(0.5)
  th, td
    padding 24px 0
  .inputNunber
    width 80px
    background-color #f5f5f5
    border-radius 6px
</style>



