<template>
  <div class="home">
    <blb-header :titleType='"sale"' :title="title">
      <span @click="back" class="sale-back"> &lt;</span>
    </blb-header>
    <div v-if="isSearch">
      <div class="search">
        <input class="ipt" type="text" placeholder="请填写店铺名称">
        <div @click="handleSearch" class="btn"><img src="@/assets/search.png" alt="">
        </div>
      </div>
      <div class="blb-1px-b"></div>
    </div>
    <div v-if="!isSearch" class="search-infos">
      <blb-item :config='{text: "韩式24小时便利店", closeMark: true}'></blb-item>
      <div class="info">
        <div class="container">
          <p>联系人: 熊川宇</p>
          <p>联系方式: 18811742305</p>
          <p>店铺地址: 龙泽龙泽龙舟赛</p>
        </div>
        <div class="blb-1px-b"></div>
        <div class="msg">请与店主确定是否无误</div>
        <blb-btn @click="ok">信息准确</blb-btn>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SaleHome',
  components: {
  },
  data () {
    return {
      title: '<span style="color: #fff; font-size: 20px">我的任务</span>',
      isSearch: true
    }
  },
  methods: {
    handleSearch () {
      this.isSearch = false
    },
    ok () {
      this.$router.push({'name': 'SaleStockTake'})
    },
    back () {
      this.isSearch = true
    }
  },
  mounted () {

  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .sale-back
    padding 40px
    color #fff
    font-weight bold
    font-size 50px
    position relative
    z-index 2
  .search
    height 100px
    display flex
    align-items center
    padding 0 44px 0 62px
    font-size 28px
  .ipt
    width 100%
    padding-bottom 10px
    border-bottom 2px solid #CECECE
  .btn
    padding 30px
    > img
      width 34px
  .search-infos
    padding-top 15px
    .info
      font-size 32px
      line-height 60px
      color #4A4A4A
      padding 0 36px 0 36px
      .container
        padding 40px 36px 50px 36px
  .msg
    font-size 24px
    padding 20px 0 20px 0
    color #2D7DFF
</style>



