<template>
  <div class="login">
    <blb-header :showClose='true' :title="'便利吧司机端'"></blb-header>
    <div class="container">
    <div class="top116">
      <blb-ipt :showArea='true' :placeholder='"请输入账号"'></blb-ipt>
    </div>
      <div class="t30">
        <blb-ipt :showClear='true' :showTogle='true' :placeholder='"请输入密码"'></blb-ipt>
      </div>
      <blb-btn @click='handleClick'>登录</blb-btn>
      <div class="bottom">
        <div class="register">忘记密码？请询问主管</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SaleLogin',
  components: {
  },
  data () {
    return {
    }
  },
  methods: {
    handleClick (event) {
      this.$router.push({
        name: 'SaleHome'
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus">
  .login
    color green
  .container
    padding 0 90px
  .bottom
    display flex
    justify-content space-between
    font-family: PingFangSC-Regular
    font-size: 28px
    color: #858585
    letter-spacing: 1.4px
    padding-top 10px
  .password
    color #858585
  .t30
    padding-top 30px
  .top116
    padding-top 116px
</style>



