<template>
  <div :class="['header', titleType]">
    <div v-if="showClose" @click="handleGoClose" class="close"><img src="@/assets/title-close.png"></div>
    <div v-if="showBack" @click="handleGoBack" class="back"><img src="@/assets/back.png"></div>
    <slot></slot>
    <div class="title" v-html="title"></div>
  </div>
</template>

<script>
const COMPONENT_NAME = 'blb-header'

export default {
  name: COMPONENT_NAME,
  props: {
    title: {
      type: String,
      default: ''
    },
    titleType: {
      type: String,
      default: ''
    },
    showClose: {
      type: Boolean,
      default: false
    },
    showBack: {
      type: Boolean,
      default: false
    },
    showGoBack: {
      type: Function,
      default: null
    },
    showGoClose: {
      type: Function,
      default: null
    }
  },
  data () {
    return {
    }
  },
  methods: {
    handleGoBack (event) {
      if (this.showGoBack) {
        this.showGoBack()
      }
      this.$router.back()
    },
    handleGoClose () {
      if (this.showGoClose) {
        this.showGoClose()
      }
      this.$router.back()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus">
  .header
    display flex
    align-items center
    box-sizing border-box
    height 88px
    font-size 40px
    color #000
    text-align center
    position relative
    // 不同类型的header类型样式区分
    &.info
      background-color #FFD756
    &.sale
      background-color #262A36
      height 120px
  .close
    padding 0 40px
    width 20px
    position relative
    z-index 1
    > img
      width 100%
  .back
    padding 0 40px
    width 20px
    position relative
    z-index 1
    > img
      width 100%
  .title
    position absolute
    left 0
    right 0
</style>



