// 销售
import SaleLogin from '@/pages/sale/login'
import SaleHome from '@/pages/sale/home'
import SaleStockTake from '@/pages/sale/home/stock-take'
import SaleAddGoods from '@/pages/sale/home/add-goods'

const SaleRouter = [{
  path: '/SaleLogin',
  name: 'SaleLogin',
  component: SaleLogin
}, {
  path: '/SaleHome',
  name: 'SaleHome',
  component: SaleHome
}, {
  path: '/SaleStockTake',
  name: 'SaleStockTake',
  component: SaleStockTake
}, {
  path: '/SaleAddGoods',
  name: 'SaleAddGoods',
  component: SaleAddGoods
}]

export default SaleRouter



