<template>
  <div class="home">
    <div ref="container">
      <router-view/>
    </div>
    <div ref="footer" class="footer">
      <div :key='foot.key' v-for="foot in footers">
        <router-link
          active-class="checked"
          :to="foot.to">
          <div>
            <div :class="['icon', foot.key]"><img :src="foot.icon" alt=""></div>
            <div class="text">{{ foot.text }}</div>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { footers } from '../config'
export default {
  name: 'ShopHome',
  components: {
  },
  data () {
    return {
      footers
    }
  },
  methods: {
    // 设置适配布局
    setLayout () {
      const { footer: { offsetHeight }, container } = this.$refs
      const screenHeight = self.screen.height
      const DIF = screenHeight - offsetHeight
      container.style.height = DIF + 'px'
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.setLayout()
    })
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .footer
    height 98px
    display flex
    background-color #F5F5F5
    border-top 1px solid #E2E2E2
    font-size 20px
    color #999
    position absolute
    bottom 0
    left 0
    width 100%
    > div
      flex 1
      display flex
      align-items center
      justify-content center
  .icon
    width 40px
    margin 0 auto
    > img
      width 100%
  .text
    padding-top 6px
  .checked
    color #000
</style>



