<template>
  <button
      class="blb-btn"
      :type="type"
      :class="btnClass"
      @click="handleClick($event)">
    <i :class="icon" v-if="icon !== ''"></i>
    <slot></slot>
  </button>
</template>

<script>
const COMPONENT_NAME = 'blb-btn'
export default {
  name: COMPONENT_NAME,
  props: {
    icon: {
      type: String,
      default: ''
    },
    disabled: {
      type: Boolean,
      default: false
    },
    btnClass: {
      type: Object,
      default: null
    },
    type: {
      type: String,
      default: 'button'
    }
  },
  methods: {
    handleClick (event) {
      if (this.disabled) {
        event.preventDefault()
        event.stopPropagation()
        return
      }
      this.$emit('click', event)
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .blb-btn
    width 570px
    height 100px
    background-color #FFD756
    font-size 36px
    text-align center
    border-radius 4px
    display block
    margin 40px auto
</style>



