<template>
  <div class="apply-shop">
    <blb-header :showBack='true' :title="'申请店铺'"></blb-header>
    <div class="content pt40">
      <blb-form-ipt
        :key="ipt.key"
        v-for="ipt in applyInputs"
        :text='ipt.text'
        :placeholder='ipt.placeholder'
        ></blb-form-ipt>
      <blb-btn @click='handleClick'>申请</blb-btn>
      <p class="explain">申请后请等待店铺管家与您联系</p>
    </div>
  </div>
</template>

<script>
import { applyInputs } from './config/index.js'
export default {
  name: 'ShopApplyShop',
  components: {
  },
  data () {
    return {
      applyInputs
    }
  },
  methods: {
    handleClick (event) {
      this.$router.push({
        name: 'ShopApplyFinish'
      })
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .pt40
    padding-top 40px
  .explain
    text-align center
    color #3E3E3E
    font-size 28px
</style>



