<template>
  <div class="mgrStock">
    <div class="blb-1px-b"></div>
    <div class="container">
      <blb-table :columns='columns' :dataSources="dataSources"></blb-table>
    </div>
  </div>
</template>

<script>
import { stockMgrs } from '@/pages/shop/config'
export default {
  name: 'ShopMgrStock',
  components: {
  },
  data () {
    return {
      columns: stockMgrs.stock.columns,
      dataSources: stockMgrs.stock.dataSources
    }
  },
  methods: {
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
.mgrStock
  text-align center
.container
  padding 0 22px
</style>



