<template>
  <div class="info">
    <blb-header :titleType='"info"' :title="shopInfo.title">
      <span class="info-back" @click="goBack"> &lt;</span>
    </blb-header>
    <div class="container">
      <blb-item :key="info.key" v-for="info in shopInfo.itemList" :config='info'></blb-item>
    </div>
  </div>
</template>

<script>
import { shopInfo } from '@/pages/shop/config'
export default {
  name: 'ShopInfo',
  components: {
  },
  data () {
    return {
      shopInfo
    }
  },
  methods: {
    goBack () {
      this.$router.back()
    }
  },
  mounted () {
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
.container
  padding-top 20px
.info-back
  position relative
  z-index 2
  padding 20px
</style>



