<template>
  <div class="register">
    <blb-header :showClose='true' :title="'便利吧'"></blb-header>
    <div class="container">
    <div class="top116">
      <blb-ipt :placeholder='"请输入您的手机号码"'></blb-ipt>
    </div>
      <div class="t30">
        <blb-ipt :showTogle='true' :placeholder='"请输入6-20位字符密码"'></blb-ipt>
      </div>
      <blb-btn @click='handleClick'>注册</blb-btn>
      <div class="bottom">
        <div class="login">登录</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ShopRegister',
  components: {
  },
  data () {
    return {
      todo: 'todo...'
    }
  },
  methods: {
    handleClick (event) {
      this.$router.push({
        name: 'ShopApplyShop'
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus">
  .container
    padding 0 90px
  .bottom
    display flex
    justify-content space-between
    font-family: PingFangSC-Regular
    font-size: 28px
    color: #FFC332
    letter-spacing: 1.4px
    padding-top 10px
  .password
    color #858585
  .t30
    padding-top 30px
  .top116
    padding-top 116px

</style>



