<template>
  <div class="cashier">
    <div class="header">
      <h6 class="main-title">收银台</h6>
      <div class="shop">
        <div class="shop-logo">
          <img src="" alt="">
        </div>
        <div class="shop-info">
          <div @click="goInfo">
            <div class="shop-title">韩营24小时便利店</div>
            <div class="shop-msg">店铺信息&nbsp;&nbsp;></div>
          </div>
        </div>
      </div>
      <div class="profit">
        <div class="profit-container">
          <div>
            <div class="icon"><img src="@/assets/water-course.png" alt=""></div>
            <div class="money">56000.34</div>
            <div class="text">今日流水</div>
          </div>
          <div>
            <div class="icon"><img src="@/assets/gross-profit.png" alt=""></div>
            <div class="money">8300.34</div>
            <div class="text">今日毛利</div>
          </div>
        </div>
      </div>
    </div>
    <div class="content">
      <div class="blb-1px-b"></div>
        <blb-item @click="handleTrend" :setDir='3' :config='trend'></blb-item>
      <div class="items">
        <div class="blb-1px-b"></div>
        <blb-item :key="item.key" v-for="item in itemList" :config='item'></blb-item>
      </div>
    </div>

  </div>
</template>

<script>
import { cashierDesks } from '@/pages/shop/config'
const { trend, itemList } = cashierDesks
export default {
  name: 'ShopCashierDesk',
  components: {
  },
  data () {
    return {
      trend,
      itemList
    }
  },
  methods: {
    goInfo () {
      this.$router.push({'name': 'ShopInfo'})
    },
    handleTrend () {
      alert('图表这个先调研一下')
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .header
    background-color #FFD756
    position relative
  .main-title
    text-align center
    font-size 28px
    padding-top 24px
  .shop
    display flex
    padding 0 48px 168px 48px
    &-logo
      >img
        display block
        width 120px
        height 120px
        border 2px solid #fff
        border-radius 50%
    &-info
      display flex
      align-items center
      padding-left 32px
      color #4A4A4A
    &-title
      font-size 32px
    &-msg
      font-size 28px
      padding-top 10px
  .profit
    width 100%
    position absolute
    bottom 0
    transform translateY(50%)
    left 0
    padding 0 5%
    &-container
      width 90%
      background-color #fff
      border-radius 16px
      box-shadow: 0 2px 5px rgba(0,0,0,0.3)
      display flex
      padding 34px 0 30px 0
      > div
        flex 1
        text-align center
        line-height 1
      .icon
        text-align center
        >img
          width 64px
      .money
        padding-top 16px
      .text
        font-size 24px
        padding-top 4px
        color #666
  .content
    padding-top 150px
  .items
    padding-top 40px
</style>



