<template>
  <div class="mgrPrice">
    <div class="container">
        <blb-item :config='price.title'></blb-item>
        <div class="container">
          <div class="content">
            <blb-table @handleChange= 'handleChange' :columns='columns' :dataSources="dataSources"></blb-table>
          </div>
        </div>
    </div>
    <div class="btns">
      <div class="btn price">按建议价格</div>
      <div class="btn setOk" @click="handleOk">设置完毕</div>
    </div>
  </div>
</template>

<script>
import { stockMgrs } from '@/pages/shop/config'
export default {
  name: 'ShopMgrPrice',
  components: {
  },
  data () {
    return {
      price: stockMgrs.price,
      columns: stockMgrs.price.columns,
      dataSources: stockMgrs.price.dataSources
    }
  },
  methods: {
    handleChange (type, datas) {
      console.log('datas', type, datas)
    },
    handleOk () {
      alert('设置完成')
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
.mgrPrice
  text-align center
.container
  padding 0 22px
.content
  padding-top 20px
.btns
  font-size 28px
  border 2px solid #F5F5F5
  display flex
  margin-top 40px
  .btn
    height 100px
    line-height 100px
    text-align center
    flex 1
  .setOk
    border-left 2px solid #F5F5F5
    background-color #FFD756
</style>



