    return {
      title: '<span style="color: #fff; font-size: 20px">新增商品</span>'
    }
  },
  methods: {
    handleSearch () {
    },
    goBack () {
      this.$router.back()
    }
  },
  mounted () {
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .sale-back
    padding 40px
    color #fff
    font-weight bold
    font-size 50px
    position relative
    z-index 2
  .footer
    height 100px
    line-height 100px
    display flex
    align-items center
    font-size 28px
    position absolute
    width 100%
    bottom 0
    left 0
    border-top 2px solid #E9E9EB
    > div
      flex 1
      height 100%
      text-align center
