<template>
  <div class="stock">
    <blb-header :titleType='"sale"' :title="title">
      <span @click="back" class="sale-back"> &lt;</span>
    </blb-header>
    <div class="container">
      <div class="title">
        韩式24小时便利店
      </div>
      <div class="blb-1px-b"></div>
      <div class="rich-scan" @click="handleAddGoods">
        <div class="btn">
          <img src="@/assets/rich-scan.png" alt="">
          <span>扫描添加商品</span>
        </div>
      </div>
      <div class="table">
        <blb-table :columns='stockTake.table.columns' :dataSources="stockTake.table.dataSources"></blb-table>
      </div>
    </div>
    <div class="submit" @click="ok">
      盘货完成
    </div>
  </div>
</template>

<script>
import { stockTake } from '@/pages/sale/config'
export default {
  name: 'SaleStockTake',
  components: {
  },
  data () {
    return {
      title: '<span style="color: #fff; font-size: 20px">我的任务</span>',
      stockTake
    }
  },
  methods: {
    handleAddGoods () {
      const res = confirm('中间会调取微信扫一扫，需要后端做签名')
      if (res) {
        this.$router.push({'name': 'SaleAddGoods'})
      }
    },
    back () {
      this.$router.back()
    },
    ok () {
      alert('ok')
    }
  },
  mounted () {
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .sale-back
    padding 40px
    color #fff
    font-weight bold
    font-size 50px
    position relative
    z-index 2
  .container
    padding-top 30px
  .title
    height 120px
    line-height 120px
    text-align center
    font-size 32px
    color #424242
  .rich-scan
    padding 42px 102px
    .btn
      height 120px
      border-radius 60px
      background-color #FFD756
      text-align center
      font-size 36px
      display flex
      align-items center
      justify-content center
      > img
        width 40px
      > span
        padding-left 30px
  .table
    text-align center
  .submit
    height 100px
    line-height 100px
    text-align center
    position absolute
    width 100%
    bottom 0px
    left 0
    background #FFD756
    border-radius 4px
    font-size 36px
</style>



