<template>
  <div class="stockMgr">
    <div class="container">
      <router-link
        class="nav"
        active-class="checked"
        :key="nav.key"
        v-for="nav in navs"
        :to="nav.to">
        <div>{{nav.text}}</div>
      </router-link>
    </div>
    <router-view/>
  </div>
</template>

<script>
import { stockMgrs } from '@/pages/shop/config'
export default {
  name: 'ShopStockMgr',
  components: {
  },
  data () {
    return {
      navs: stockMgrs.navs
    }
  },
  methods: {

  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .container
    padding 40px 36px
    background-color #F5F5F5
    display flex
    font-size 36px
    justify-content space-between
  .nav
    width 320px
    height 100px
    text-align center
    line-height 100px
    border 2px solid #000
    background-color #fff
    border-radius 10px
    color #000
    &.checked
      border none
      background-color #FFD756
</style>



