<template>
  <div class="item blb-1px-b" @click="handleClick">
    <div class="info">
      <div v-if="itemConfig.icon" class="icon"><img :src="itemConfig.icon" alt=""></div>
      <div class="text">{{itemConfig.text}}</div>
    </div>
    <div class="right">
      <div class="des">
        <div class="pr25 lH12">
          <div v-if="itemConfig.top" class="top">{{itemConfig.top}}</div>
          <div v-if="itemConfig.bottom" class="bottom">{{itemConfig.bottom}}</div>
        </div>
      </div>
      <div v-if="itemConfig.img" class="img">
        <img :src="itemConfig.img" alt="">
      </div>
      <div v-if="!itemConfig.closeMark">
        <img :class="['landmark', markDir()]" src="@/assets/item.png" alt="">
      </div>
    </div>
  </div>
</template>

<script>
const COMPONENT_NAME = 'blb-item'
export default {
  name: COMPONENT_NAME,
  props: {
    setDir: {
      type: Number,
      default: 0
    },
    config: {
      type: Object,
      default: () => {}
    }
  },
  data () {
    return {
      direction: this.setDir,
      itemConfig: this.config || {}
    }
  },
  computed: {
  },
  methods: {
    markDir () {
      // 处理可变和不可变方向配置
      const direction = this.config.direction || this.direction
      switch (direction) {
        case 0: // left
          return 'blb-rotate0'
        case 1: // top
          return 'blb-rotate90'
        case 2: // right
          return 'blb-rotate180'
        case 3: // bottom
          return 'blb-rotate270'
      }
    },
    handleClick () {
      this.$emit('click')
    }
  },
  mounted () {
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .item
    height 120px
    padding 0 30px
    display flex
    justify-content space-between
    align-items center
  .info
    display flex
    align-items center
  .icon
    width 50px
    > img
      width 100%
  .text
    font-size 32px
    padding-left 20px
  .landmark
    width 15px
  .right
    display flex
    .des
      display flex
      align-items center
      text-align center
      .top
        font-size 32px
        color #767E89
      .bottom
        font-size 20px
        color #BEC8CE
  .pr25
    padding-right 25px
  .lH12
    line-height 1.2
  .img
    padding-right 26px
    > img
      width 60px
      height 60px
      border-radius 50%
</style>



