// shop注册和申请流程
import ShopLogin from '@/pages/shop/login'
import ShopRegister from '@/pages/shop/register'
import ShopApplyShop from '@/pages/shop/apply-shop'
import ShopApplyFinish from '@/pages/shop/apply-finish'
// shop主页
import ShopHome from '@/pages/shop/home'
import ShopInfo from '@/pages/shop/home/info'
import ShopCashierDesk from '@/pages/shop/home/cashier-desk'
// 库存管理
import ShopStockMgr from '@/pages/shop/home/stock-mgr'
import MgrStock from '@/pages/shop/home/stock-mgr/stock'
import MgrPrice from '@/pages/shop/home/stock-mgr/price'
// 测试预览页面
import ShopView from '@/pages/shop/view'

const ShopRouter = [{
  path: '/',
  redirect: { name: 'View' }
},
{
  path: '/View',
  name: 'View',
  component: ShopView
},
{
  path: '/ShopLogin',
  name: 'ShopLogin',
  component: ShopLogin
},
{
  path: '/ShopRegister',
  name: 'ShopRegister',
  component: ShopRegister
},
{
  path: '/ShopApplyShop',
  name: 'ShopApplyShop',
  component: ShopApplyShop
},
{
  path: '/ShopApplyFinish',
  name: 'ShopApplyFinish',
  component: ShopApplyFinish
},
{
  path: '/ShopHome',
  name: 'ShopHome',
  component: ShopHome,
  redirect: { name: 'ShopCashierDesk' },
  children: [
    {
      path: '/',
      redirect: 'ShopCashierDesk'
    },
    {
      path: 'ShopCashierDesk',
      name: 'ShopCashierDesk',
      component: ShopCashierDesk
    },
    {
      path: 'ShopStockMgr',
      name: 'ShopStockMgr',
      component: ShopStockMgr,
      children: [
        {
          path: '/',
          redirect: 'MgrStock'
        },
        {
          path: 'MgrStock',
          name: 'MgrStock',
          component: MgrStock
        },
        {
          path: 'MgrPrice',
          name: 'MgrPrice',
          component: MgrPrice
        }
      ]
    }
  ]
},
{
  path: '/ShopInfo',
  name: 'ShopInfo',
  component: ShopInfo
}]

export default ShopRouter



