<template>
  <div class="view">
      <blb-item @click="goPage(view.key)" :key='view.text' :config='view' v-for="view in views"></blb-item>
  </div>
</template>

<script>
export default {
  name: 'ShopView',
  components: {
  },
  data () {
    return {
      views: [
        {
          key: 'ShopLogin',
          text: '商家注册页面',
          direction: 2 // 右边
        },
        {
          key: 'ShopHome',
          text: '商家主页',
          direction: 2 // 右边
        },
        {
          key: 'SaleLogin',
          text: '销售页面',
          direction: 2 // 右边
        }
      ]
    }
  },
  methods: {
    goPage (key) {
      this.$router.push({'name': key})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus">
</style>



